<template>
  <div id="app">
      <navigation></navigation>
   <div class="my-4">
     <sidebar></sidebar>
     <router-view/>
   </div>
   
   <footer class="footer-distributed">
		<div class="footer-left">
			<p class="footer-company-name">BPR&copy; 2018</p>
		</div>

		<div class="footer-center">
			<p class="footer-company-name">{{dr}}</p>
		</div>

		<div class="footer-right">
		<p class="footer-company-name">{{tm}}</p>
		</div>
	</footer>
  </div>
</template>


<script>
 import navigation from './components/navigation';
  import sidebar from './components/sidebar';
  export default {
      name:'app',
	  components:{navigation, sidebar},
	  data(){
            return {
				tm:'',
				dr:''

            }
        },
        created(){
			this.timeset();
			 setInterval(this.timeset, 1000);
            
		},
		methods:{
			timeset(){
			var monthNames = [ "January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December" ];
			var dayNames= [ "Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday" ];
			var now=new Date();
			var yr=now.getFullYear();
			var mm=this.checkTime(now.getMonth());
			var dd=this.checkTime(now.getDate());
			var hh=this.checkTime(now.getHours());
			var mn=this.checkTime(now.getMinutes());
			var sc=this.checkTime(now.getSeconds());
			 this.tm = hh+":"+mn+":"+sc;
			 this.dr=monthNames[now.getMonth()]+"  "+dd+"   "+dayNames[now.getDay()]
			},
			checkTime(i) {
				if (i < 10) {
					i = "0"+i;
					return i;
					};
					return i;
			}
		}
  }
</script>


