<script>
import axios from 'axios'
    export default {
        name: "ind-add",

        data(){
            return {
                alert_error:'',
                cus_no:'',
                surname:'',
                fname:'',
                sname:'',
                fnames:'',
                title:'',
                marital_status:'',
                spouse:'',
                client_type:'',
                gvt:'',
                leg_status:0,
                emp_buss:'',
                co_reg_no:'',
                pep_cus:'',
                rssb_no:'',
                accofficer:'',
                sec:0,
                eco_sub:0,
                education:0,
                in_freq:0,
                in_range:0,
                industry:0,
                naics:0,
                occup:0,
                rel_party:0,
                rel_type:0,
                soc_eco_class:0,
                sub_seg:0,
                tar_seg:0,
                nat:'',
                res:'',
                sbu:'',
                lid:'',
                url:'http://159.89.121.87:9090/customer',
                account_officer:[],
                eco_sub_sec_code:[],
                edu:[],
                income_frequency:[],
                income_range:[],
                ind:[],
                naics_code:[],
                occupation:[],
                related_party:[],
                relation_type:[],
                sector:[],
                social_economic_class:[],
                sub_segment:[],
                target_segment:[],
                vision_sbu:[],
                legal_status:[],
                country:[]
            }
        },
        computed:{
            availableNames(){

            }
        },
        created(){
            this.getSector();
            this.getAccount();
            this.getEcoSubSecCode();
            this.getEducation();
            this.getIncomeFrequency();
            this.getIncomeRange();
            this.getIndustry();
            this.getNaicsCodes();
            this.getOccupation();
            this.getRelatedParty();
            this.getRelatedType();
            this.getSocialEcoClass();
            this.getSubSegment();
            this.getTargetSegment();
            this.getCountry();
            this.getVisionSbu();
            this.getLegalStatus();
            axios.get(`${this.url}/`+this.$route.params.id).then(res=>{
                this.cus_no=res.data.id,
                this.surname=res.data.shortname,
                this.fname=res.data.gbname1,
                this.sname=res.data.gbname2,
                this.fnames=res.data.givennames,
                this.title=res.data.title,
                this.marital_status=res.data.marital_status,
                this.spouse=res.data.spouse,
                this.client_type=res.data.client_type,
                this.gvt=res.data.local_gvt_member,
                this.leg_status=res.data.legal_status,
                this.emp_buss=res.data.employers_buss,
                this.co_reg_no=res.data.company_reg_no,
                this.pep_cus=res.data.pep_cus,
                this.rssb_no=res.data.rssb_no,
                this.sbu=res.data.vision_sbu,
                this.accofficer=res.data.accountOfficer.id,
                this.sec=res.data.sector.id,
                this.industry=res.data.industry.id,
                this.sub_seg=res.data.subSegment.id,
                this.nat=res.data.nationality,
                this.res=res.data.residence,
                this.tar_seg=res.data.targetSegment.id,
                this.soc_eco_class=res.data.socialEconomicClass.id,
                this.rel_party=res.data.relatedParty.id,
                this.rel_type=res.data.relationType.id,
                this.occup=res.data.occupation.id,
                this.naics=res.data.naicsCode.id,
                this.eco_sub=res.data.ecoSubSecCode.id,
                this.education=res.data.educationDetails.id,
                this.in_freq=res.data.incomeFrequency.id,
                this.in_range=res.data.incomeRange.id
            });
        },

        methods:{
            back () {
                this.$router.push('/cus-find');
            },
            show () {
                this.$modal.show('hello-world');
            },
            hide () {
                this.$modal.hide('hello-world');
                this.$router.push('/cus-find');
            },
            addCustomer(){
                var x=this.tar_seg;
                var a=this.soc_eco_class;
                var b=this.rel_party;                            
                var c=this.rel_type;
                var d=this.occup;
                var e=this.naics;
                var f=this.eco_sub;
                var g=this.education;
                var h=this.in_freq;
                var i=this.in_range;
                var now=new Date();
                        const customer={
                            id:this.cus_no,
                            updateddate:now,
                            shortname:this.surname,
                            gbname1:this.fname,
                            gbname2:this.sname,
                            givennames:this.fnames,
                            title:this.title,
                            marital_status:this.marital_status,
                            spouse:this.spouse,
                            client_type:this.client_type,
                            local_gvt_member:this.gvt,
                            legal_status:this.leg_status,
                            employers_buss:this.emp_buss,
                            company_reg_no:this.co_reg_no,
                            pep_cus:this.pep_cus,
                            rssb_no:this.rssb_no,
                            vision_sbu:this.sbu,
                            accountOfficer:{
                                id:this.accofficer},
                            sector:{
                                id:this.sec},
                            industry:{
                                id:this.industry},
                            subSegment:{
                                id:this.sub_seg},
                            nationality:this.nat,
                            residence:this.res,
                             if(x=''){
                                this.tar_seg=0;
                            },
                            targetSegment:{
                                id:this.tar_seg
                            },
                            if(a=''){
                                this.soc_eco_class=0;
                            },
                            socialEconomicClass:{
                                id:this.soc_eco_class},
                            if(b=''){
                                this.rel_party=0;
                            },
                            relatedParty:{
                                id:this.rel_party},
                            if(c=''){
                                this.rel_type=0;
                            },
                            relationType:{
                                id:this.rel_type},
                            if(d=''){
                                this.occup=1000;
                            },
                            occupation:{
                                id:this.occup},
                            if(e=''){
                                this.naics=0;
                            },
                            naicsCode:{
                                id:this.naics},
                            if(f=''){
                                this.eco_sub=0;
                            },
                            ecoSubSecCode:{
                                id:this.eco_sub},
                            if(g=''){
                                this.education=0;
                            },
                            educationDetails:{
                                id:this.education},
                            if(h=''){
                                this.in_freq=0;
                            },
                            incomeFrequency:{
                                id:this.in_freq},
                            if(i=''){
                                this.in_range=0;
                            },
                            incomeRange:{
                                id:this.in_range}

                            
                        }
                        this.lid=this.cus_no;
                        axios.put(`${this.url}/`+this.$route.params.id,customer).then( ()=>{
                            this.show();
                        }).catch(error=>console.log(error))
                   
            },
            getSector() {
            axios
                .get(`http://159.89.121.87:9090/sector`)
                .then(res => {
                this.sector = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getAccount() {
            axios
                .get(`http://159.89.121.87:9090/accountOfficer`)
                .then(res => {
                this.account_officer = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getEcoSubSecCode() {
            axios
                .get(`http://159.89.121.87:9090/ecoSubSecCode`)
                .then(res => {
                this.eco_sub_sec_code = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getEducation() {
            axios
                .get(`http://159.89.121.87:9090/educationDetails`)
                .then(res => {
                this.edu = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getIncomeFrequency() {
            axios
                .get(`http://159.89.121.87:9090/incomeFrequency`)
                .then(res => {
                this.income_frequency = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getIncomeRange() {
            axios
                .get(`http://159.89.121.87:9090/incomeRange`)
                .then(res => {
                this.income_range = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
