            axios
                .get(`http://159.89.121.87:9090/naicsCode`)
                .then(res => {
                this.naics_code = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            },
            getOccupation() {
            axios
                .get(`http://159.89.121.87:9090/occupation`)
                .then(res => {
                this.occupation = res.data;
                })
                .catch(error => {
                console.log(error);
                }); 
            }, 
            getRelatedParty() {
            axios
                .get(`http://159.89.121.87:9090/relatedParty`)
                .then(res => {
                this.related_party = res.data;
                })
                .catch(error => {
                console.log(error);
                });
            }, 
            getRelatedType() {
            axios
                .get(`http://159.89.121.87:9090/relationType`)
                .then(res => {
                this.relation_type = res.data;
                })
                .catch(error => {
                console.log(error);
