import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Customer from './views/customer/index'
import FindCustomer from './views/customer/find'
import SearchCustomer from './views/customer/search'

Vue.use(Router)

export default new Router({
    routes: [

        {
            path:'/',
            component:Home
        },
        {
            path:'/customer/:id',
            component:Customer,
            props: true
        },
        {
            path:'/cus-find/',
            component:FindCustomer
        },
        {
            path:'/cus-search/',
            component:SearchCustomer
        },

    ]
})
