<template>
    <div class="container">
        <div class="row">
            <div class="col-md-6 offset-md-1" style="background-color:Whitesmoke; left:20%">
                    <h4>Enter Customer Number</h4>
                <div class="form-group">
                    <input type="text"  v-model="find" placeholder="Search..." class="form-dereck-search">
                </div>
                <div class="" style="background-color:whitesmoke;">
                    <button class="btn btn-success btn-sm ml-2 fa fa-search" @click="findCustomer(find)">Search</button>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import axios from 'axios'
    export default {
        name: "ind-add",

        data(){
            return {    
                find:'',
                url:'http://159.89.121.87:9090/customer',
                customer:[]
            }
        },
        computed:{
            availableNames(){

            }
        },
        created(){
           axios.get(this.url).then(data=>{
             this.customer=data.data
            });
            this.formatcustomer();
        },

        methods:{
            formatcustomer() {
                var x;
                 for(var key in this.customer){
                var dir=this.customer[key];
                if(dir.id==this.find){
                    x=dir.name;
                }}
                //sort our sectors in descending order
                return this.customer.filter(customer => {
                    return customer.name.toUpperCase().includes(x.toUpperCase())
                }).sort((a,b) => a.name>b.name);
            },
            findCustomer(find){
                if(this.find==''){
                    return;
                }
                this.$router.push('/customer/'+find)
            }
        }
    }
</script>

